import React from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { API_ROOT_URL } from '../api-config';
import { PageHeader, Layout, List, Skeleton, Typography, Col } from 'antd';

const { Text } = Typography;
const { Content } = Layout;
export default class Category extends React.Component {
    constructor() {
        super();
        this.state = {
            dataSource: [1, 2, 3, 4, 5, 6],
            loading: true
        }
    }

    componentDidMount() {
        const config = {
            headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        };
        axios.get(`${API_ROOT_URL}/topics/${this.props.match.params.name}/phrases`, config)
            .then(res => {
                this.setState({ dataSource: res.data, loading: false });
            })
    }

    render() {
        const { dataSource, loading } = this.state;
        return (
            <Content>
                <PageHeader
                    className="site-page-header"
                    onBack={() => window.history.back()}
                    title={this.props.match.params.name}
                />
                <List
                    itemLayout="horizontal"
                    dataSource={dataSource}
                    renderItem={item => (
                        <List.Item>
                            <Skeleton loading={loading} active paragraph={{ rows: 1 }} >
                                        <Col span={12}>
                                            <div className="ml3"><h4>{item.engPhraseName}</h4></div>
                                        </Col>
                                        <Col span={12}>
                                            <div><h4>{item.transliteration}</h4></div>
                                            <div><Text type="secondary">{item.nonEngPhraseName}</Text></div>
                                        </Col>
                            </Skeleton>
                        </List.Item>
                    )}
                />
            </Content>
        )
    }
}